@media (min-width: 768px) {
  #sm-tech {
    display: none;
  }
  .intro-header,
  .about-header {
    height: 75%;
    color: #f8f8f8;
    background-position: 40px;
    background-size: auto 100%;
    display: flex;
    align-items: center;
  }
  .mySlides {
    width: 100%;
    height: auto;
  }
  .slideshow {
    height: 100%;
  }
  .CW2,
  .LPI2,
  .tabs {
    display: block;
  }
  .tabs i {
    padding: 5px;
    margin-right: 10px;
  }
  .tabs label span {
    display: inline-block;
  }
  .tabs {
    max-width: 750px;
    margin: 50px auto;
  }
  .people-sm {
    display: none;
  }
}
@media (min-width: 768px) and (max-width: 991px) {
  .CW1 h1,
  .LPI1 h1 {
    font-size: 28px;
  }
  .CW1 h3,
  .LPI1 h3 {
    font-size: 18px;
  }
  #company,
  #company:after {
    height: 420px;
  }
}
@media (max-width: 991px) {
  .mySlides {
    width: 100%;
    height: 100%;
  }
}
@media (min-width: 992px) and (max-width: 1199px) {
  #company,
  #company:after {
    height: 480px;
  }
}
@media (min-width: 768px) and (max-width: 1199px) {
  .navbar-right {
    padding-right: 0;
  }
}
